header{
    min-height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
    background-color:rgb(3, 120, 124);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 99;
}
header .logo a{
    text-decoration: none;
    text-transform: uppercase;
    color: white;
}
header .logo a:hover {
    color:greenyellow;
}
header nav{
    display: -webkit-flex;
    display: flex;
 
}
nav ul li{
    list-style: none;
    display: inline-block;
}
nav ul li a{
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    padding: 0 15px;
}
nav ul li a:hover{
    color:greenyellow;
}
nav .nav-cart{
    cursor: pointer;
    position: relative;
    top:-1px;
}
nav .nav-cart span{
    position: absolute;
    top:-12px;
    right: -7px;
    background: white;
    font-size: 10px;
    color:wheat;
    padding: 3px 5px;
    border-radius: 50%;
    z-index: 1;
}
header .menu,
header .close{
    cursor: pointer;
    display: none;
}
@media (max-width: 730px){
    header ul{
        position: fixed;
        top:0;
        left: -100%;
        width: 100%;
        height: 100vh;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-align-items: center;
                align-items: center;
        -webkit-flex-direction: column;
                flex-direction: column;
        background: white;
        color: black;
        z-index: 99;
        opacity: 0.97;
        transition: 0.5s linear;
    }
    header ul li a{
        color: black;
    }
    header .menu,
    header .close{
        display: block;
    }
    header .close{
        position: absolute;
        top:10px;
        right: 20px;
    }
    header ul.toggle{
        left: 0;
    }
}

