  overflow-y: hidden;
  margin: 10px;
}

.card__img {
  position: relative;
  height: 13rem;
  width: 260px;
  -webkit-filter: drop-shadow(5px 10px 15px rgba(8,9,13,.4));
          filter: drop-shadow(5px 10px 15px rgba(8,9,13,.4));
}

.card__data {
 margin-top: 30px;
}

.card__title {
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #F2A20C;
  color: var(--first-color);
 
}

.card__preci {
  display: inline-block;
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  font-weight: 500;

}

.card__description {
  font-size: .875rem;
  font-size: var(--small-font-size);
}

.card__button {
  display: block;
  width: cover;
  margin-top: 5px;
  padding: 10px;
  border-radius:10px;
  background-color: #F2A20C;
  background-color: var(--first-color);
  color: #E9EAEC;
  color: var(--white-color);
  font-weight: 600;
  transition: .2s;

}

.card__img
 {
  transition: .5s;
}


.card:hover .card__img {
  -webkit-transform: translate(0rem, -5rem) rotate(0deg);
          transform: translate(0rem, -5rem) rotate(0deg);
}



